import React, { useState } from "react";
import { Link } from "gatsby";
import config from "../../data/SiteConfig";
import Categories from "./Categories";
import { Drawer, IconButton, List, ListItem } from "@material-ui/core";
import {
  Menu as MenuIcon,
  Close as CloseIcon,
} from "../../node_modules/@material-ui/icons";
import styles from "./Header.module.scss";

const Header = () => {
  const [open, setOpen] = useState(false);
  return (
    <header>
      <h1>
        <Link to="/" activeClassName={styles.activeNav}>
          {config.siteTitle}
        </Link>
      </h1>
      <nav>
        <ul className={styles.mainNav}>
          <div>
            <IconButton
              onClick={() => {
                setOpen(!open);
              }}
            >
              <MenuIcon />
            </IconButton>
          </div>
        </ul>
      </nav>
      <Drawer anchor="top" open={open} onClose={() => setOpen(false)}>
        <List style={{padding: '10px'}}>
          <ListItem>
            <a
              href="https://alireza-kavian.github.io"
              target="_blank"
              rel="noreferrer noopener"
              style={{width: '100%'}}
            >
              <span> ABOUT</span>
            </a>
            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                padding: "0 10px",
                width: '100%',
              }}
            >
              <IconButton onClick={() => setOpen(false)}>
                <CloseIcon />
              </IconButton>
            </div>
          </ListItem>
          <Categories activeClassName={styles.activeNav} />
        </List>
      </Drawer>
    </header>
  );
};

export default Header;
